export function name(name){
  if(typeof(name) !== 'string'){
    return name = 'Formato invlido!'
  }
  if(name === ''){
    return 'Nome  obrigatrio'
  }
}

export function phone(phone){
  if(typeof(phone) !== 'string'){
    return 'Formato invlido!'
  }
  if(phone === ''){
    return 'Telefone  obrigatrio'
  }
  if(phone.length !== 15){
    return 'Telefone invlido'
  }
  if(phone.includes('_')){
    return 'Telefone invlido'
  }
}

export function CPF(cpf){
  if(typeof(cpf) !== 'string'){
    return 'Formato invlido!'
  }
  if(cpf === ''){
    return 'CPF  obrigatrio'
  }
  if(cpf.length !== 14){
    return 'CPF invlido'
  }
  if(cpf.includes('_')){
    return 'CPF invlido'
  }
}

export function email(email){
  if(typeof(email) !== 'string'){
    return 'Formato invlido!'
  }
  if(email === ''){
    return 'Email  obrigatrio'
  }
  if(!email.includes('@')){
    return 'Email no  valido'
  }

}

export function date(date){
  if(typeof(date) !== 'string'){
    return 'Formato invlido!'
  }
  if(date === ''){
    return 'Data de Nascimento  obrigatrio'
  }
  if(date.length !== 10){
    return 'Data invlida'
  }
}

export function password(password){
  if(typeof(password) !== 'string'){
    return 'Formato invlido!'
  }

  if(password === ''){
    return 'Senha  obrigatria'
  }
  
}

export function confirmPassword(password){
  if(typeof(password) !== 'string'){
    return 'Formato invlido!'
  }

  if(password === ''){
    return 'Confirmar Senha  obrigatria'
  }
  
}

export function selectCourse(teacher, course){

  if(teacher === ''){
    return 'Necessrio selecionar professor/curso'
  }
  if(typeof(teacher) !== 'number' || teacher === 0){
    return 'Professor invlido'
  }
  if(course === ''){
    return 'Necessrio selecionar professor/curso'
  }
  if(typeof(course) !== 'number' || course === 0){
    return 'Curso invlido'
  }
}

export function time(time){
  let checkTime = time.split(':')
  let hora = parseInt(checkTime[0])
  let min = parseInt(checkTime[1])

  if(time === ''){
    return 'Insira um horrio'
  }
  if(time.includes('_') || time.length !== 5){
    return "Horrio invlido"
  }
  if(hora < 0 || hora > 23){
    return 'Horrio invalido'
  }
  if(min < 0 || min > 59){
    return 'Horrio invalido'
  }
  

}

export function id(id){
  if(typeof(id) !== 'number' || id === 0){
    return 'Necessrio selecionar um item'
  }  
}
